.headerWrapper {
    position: relative;
    width: 100%;
    background-color: #333;
    min-height: 55px;
    

    .header {
        position: relative;
        max-width: 1100px;
        margin: 0 auto;
    }

    img {   
        position: absolute;
        left: 20px;
        top: 0px;     
        width: 90px;
        height: auto;
    }
}